<template>
  <transition :duration="500" name="dialog-wrapper-fade">
    <!-- :class="{'qt-modal': isQt,
      'order-meeting-modal': isOrderMeeting,
      'workspace-modal': !isQt && !isOrderMeeting}" -->
    <div
      class="el-dialog__wrapper is-compact "
      @click.self="handleClickOutside">
      <div class="v-modal"
        tabindex="0" @click="handleClickOutside">
      </div>
      <div
        role="dialog"
        aria-modal="true"
        :class="['el-dialog',{'el-dialog--center': center }]"
        ref="dialog"
        :style="{ width: width }">
        <button
          type="button"
          class="el-dialog__headerbtn"
          aria-label="Close"
          @click="$emit('update:visible', false)">
          <i class="el-dialog__close el-icon el-icon-close"></i>
        </button>
        <div class="el-dialog__body flex">
          <!-- 群聊列表 -->
          <chat-list
            v-if="showChatList"
            :group-list="sessionList"
            :shareChatId="shareChatId"
            :groupAppId="workspaceId"
            @changeChating="changeChat">
          </chat-list>

          <!-- 右边 -->
          <div v-if="currentSession" class="chat-right-part flex-column">

            <!-- 群聊窗口 -->
            <chat-history :session="currentSession"></chat-history>

          </div>
          <div v-else class="flex-center" style="width:100%">
            <img width="380" :src="require('@/assets/img/im/IM-empty.png')">
          </div>
        </div>
      </div>
    </div>
  </transition>

</template>

<script>
import { getSessions } from '../../../../loaders/im';
import ChatList from './ChatList';
import ChatHistory from './ChatHistory';

export default {
  name: 'ChatPanel',
  components: {
    ChatList,
    ChatHistory,
  },
  props: {
    shareChatId: String,
    closeOnClickModal: {
      default: true,
      type: Boolean,
    },
    center: {
      type: Boolean,
      default: true,
    },
    width: {
      type: String,
      default: '80%',
    },
    // dialog 自定义的class
    customClass: {
      type: String,
      default: '',
    },
    isOrderMeeting: {
      type: Boolean,
      default: false,
    },
    showChatList: {
      type: Boolean,
      default: true,
    },
    workspaceId: {
      type: String,
      default: undefined,
    },
    workspaceCollapse: {
      style: Boolean,
      default: true,
    },
  },
  data() {
    return {
      currentSession: undefined,
    };
  },
  mounted() {
    this.currentSession = this.sessionList.find(session => session.id === this.shareChatId);
    this.$watch(() => ({
      shareChatId: this.shareChatId,
    }), (param) => {
      this.currentSession = this.sessionList.find(session => session.id === param.shareChatId);
      this.shareChatId = param.shareChatId;
    });
    // this.$watch('sessionList', (newVal) => {
    //   console.info(this.sessionList.length);
    //   if (newVal.some(item => item.id === this.currentSession.id) === false) {
    //     this.currentSession = null;
    //   }
    // });
    this.$watch('sessionList', (newVal) => {
      if (newVal.some(item => item.id === this.currentSession?.id) === false) {
        this.currentSession = null;
      }
    });
  },
  computed: {
    isQt() {
      return this.$route.name.startsWith('Qt');
    },
    sessionList() {
      return getSessions(this.workspaceId);
    },
  },
  methods: {
    // 关闭
    closeChat() {
      this.$emit('close');
    },
    changeChat(session) {
      this.currentSession = session;
    },
    // 关闭弹窗
    handleClickOutside() {
      if (this.closeOnClickModal) {
        this.$emit('update:visible', false);
      }
    },
  },
};
</script>

