<template>
  <div class="flex-column flex-1" style="padding-top: 20px">
    <div class="new-chat-name" v-if="includeList.length === 0">
      <el-input v-model="newChatName"
        :maxlength="40"
        :placeholder="$t('file-list.im.input_group_name')"></el-input>
    </div>
    <div class="member-transfer flex flex-1">
      <div class="search flex-column">
        <el-input v-model="memberKeyword"
          :placeholder="$t('file-list.im.search_member')">
        </el-input>
        <Scrollable class="search-list" y style="flex: 1" v-loading="membersLoading">
          <el-checkbox-group v-model="newChatMemberIds" v-show="memberKeyword.length === 0">
            <div class="member-item"
              v-for="groupMember in groupMembers" :key="groupMember.id">
              <el-checkbox
                :checked="includeList.some(it=>it === groupMember.id) ||
                  groupMember.id === user.id"
                :disabled="includeList.some(it=>it === groupMember.id) ||
                  groupMember.id === user.id"
                :label="groupMember.id"
                class="flex-vertical-center">
                <BaseUser :user="groupMember">
                  {{groupMember.name|| groupMember.account|| groupMember.email|| groupMember.mail}}
                </BaseUser>
              </el-checkbox>
            </div>
          </el-checkbox-group>
          <div class="member-item" v-show="memberKeyword.length !== 0"
            v-for="groupMember in searchGroupMembers" :key="groupMember.id">
            <el-checkbox
              @change="changeSelete(groupMember, $event)"
              :checked="newChatMemberIds.some(it=>it === groupMember.id) ||
                  groupMember.id === user.id"
              :disabled="includeList.some(it=>it === groupMember.id) ||
                  groupMember.id === user.id"
              :label="groupMember.id"
              class="flex-vertical-center">
              <BaseUser :user="groupMember">
                {{groupMember.name|| groupMember.account|| groupMember.email|| groupMember.mail}}
              </BaseUser>
            </el-checkbox>
          </div>
        </Scrollable>
      </div>
      <div class="member-list flex-column">
        <div class="member-list-top">
          {{$t('file-list.im.select_participants')}}
        </div>
        <Scrollable y class="flex-1" style="margin-top: 12px;">
          <ul>
            <li class="member-item flex-space-between" v-for="item in newAddMember" :key="item.id">

              <BaseUser :user="item">
                {{ item.name || item.account || item.email || item.mail }}
              </BaseUser>
              <i @click="removeSelete(item)"
                v-if="item.id !== user.id"
                class="iconfont icondelmuber"></i>
            </li>
          </ul>
        </Scrollable>
      </div>
    </div>
    <div class="button-group flex-justify-flex-end">
      <el-button @click="$emit('cancel')" class="button-cancel">
        {{$t('public.dialog.cancel')}}
      </el-button>
      <el-button type="primary" v-if="includeList.length === 0"
        @click="$emit('confirm', {newAddMemberIds, newChatName})">
        {{$t('file-list.im.create')}}({{newAddMember.length}})
      </el-button>
      <el-button type="primary" v-else
        @click="$emit('confirm', newAddMember)">
        {{$t('public.dialog.confirm')}} ({{newAddMember.length}})
      </el-button>
    </div>
  </div>
</template>

<script>
import { debounce } from 'throttle-debounce';

import Scrollable from '@/components/Scrollable';
import BaseUser from '@/components/base/User';

import {
  fetchGroupMembers,
} from '@/api/account';
import difference from '_/difference';
import { getContextUser } from '../../../../loaders/context';

export default {
  dialogify: {
    props: {
      flex: 'column',
      height: '700px',
      compact: true,
      titleAlign: 'left',
      width: '580px',
    },
  },
  components: {
    Scrollable,
    BaseUser,
  },
  props: {
    // 已在群众的成员
    includeList: {
      default: () => [],
    },
    groupAppId: {
      default() {
        return this.$route.query.relateId || this.$route.query.workspaceId;
      },
    },
  },
  data() {
    return {
      // 该workspace列表人员
      groupMembers: [],
      searchGroupMembers: [],
      newChatName: '',
      // 选中的memberId
      newChatMemberIds: [],

      memberKeyword: '',
      membersLoading: false,
    };
  },
  computed: {
    user() {
      return getContextUser();
    },
    newAddMemberIds() {
      return difference(this.newChatMemberIds, this.includeList) || [];
    },

    newAddMember() {
      return this.groupMembers.filter(item => {
        return this.newAddMemberIds.some(id => item.id === id);
      }) || [];
    },

  },
  async mounted() {
    this.debounceSearchMembers = debounce(300, this.searchMembers);
    this.groupMembers = (await fetchGroupMembers({
      keyword: '',
      groupAppId: this.groupAppId,
    })).data;
  },
  methods: {
    async searchMembers(memberKeyword) {
      this.membersLoading = true;
      const members = (await fetchGroupMembers({
        keyword: memberKeyword,
        groupAppId: this.groupAppId,
      })).data;
      this.searchGroupMembers = members;
      this.membersLoading = false;
    },

    changeSelete(member, check) {
      if (check) {
        this.newChatMemberIds.push(member.id);
      } else if (this.newChatMemberIds.indexOf(member.id) !== -1) {
        this.newChatMemberIds.splice(this.newChatMemberIds.indexOf(member.id), 1);
      }
    },

    removeSelete(member) {
      this.newChatMemberIds.splice(this.newChatMemberIds.indexOf(member.id), 1);
    },

  },

  watch: {
    memberKeyword(newVal) {
      if (newVal !== '') {
        this.searchGroupMembers = this.debounceSearchMembers(newVal);
      }
    },
  },
};
</script>

