<template>
  <div class="flex-column" style="border-right: 4px solid #eff1f7;">
    <div class="list-head flex-space-between">
      <span>{{$t('file-list.im.message')}}</span>
      <i v-if="$can('workspace', 'OP2002')"
        @click="$can('workspace', 'OP2002') && newChat()"
        class="iconfont icon-add flex-center">
      </i>
    </div>
    <Scrollable y class="group-chat-list">
      <ul>
        <li v-for="session in groupList" :key="session.id" @click="changeChating(session)">
          <chat-list-item
            :unread="session.unread | max(99)"
            :session="session"
            :currentChat="session.id === currentChatId">
          </chat-list-item>
        </li>
      </ul>
    </Scrollable>

  </div>
</template>

<script>
import i18n from 'i18next';
import Scrollable from '@/components/Scrollable';
import BaseUser from '@/components/base/User';
import { createGroup } from '@/api/im';
import { getContextUser } from '../../../../loaders/context';
import ChatListItem from './ChatListItem';
import AddMember from './AddMember';
// import { User } from '@/models/User';


export default {
  components: {
    Scrollable,
    ChatListItem,
    // eslint-disable-next-line vue/no-unused-components
    AddMember,
    // eslint-disable-next-line vue/no-unused-components
    BaseUser,
  },
  props: {
    groupList: {
      type: Array,
    },
    shareChatId: String,
    groupAppId: {
      default() {
        return this.$route.query.relateId || this.$route.query.workspaceId;
      },
    },
  },
  data() {
    return {
      currentChatId: this.shareChatId,
    };
  },
  watch: {
    shareChatId(newVal) {
      this.currentChatId = newVal;
    },
  },
  filters: {
    max(value, max) {
      if (value > max) {
        return `${max}+`;
      }
      return value;
    },
  },
  methods: {
    // 切换选中状态
    changeChating(session) {
      this.currentChatId = session.id;
      this.$emit('changeChating', session);
    },

    // 新建群聊
    async newChat() {
      const newChatInfo = await this.$injectDialog({
        is: AddMember,
        props: {
          includeList: [],
          groupAppId: this.groupAppId,
        },
      }, {
        props: {
          title: i18n.t('file-list.im.create_chat_group'),
          width: '596px',
        },
      });

      const sessId = (await createGroup(
        this.groupAppId,
        newChatInfo.newChatName,
        newChatInfo.newAddMemberIds,
      )).data;

      const newSession = await this.$im.getSession({ id: sessId });
      this.currentChatId = sessId;
      this.$parent.currentSession = newSession;
      await this.$im.send(newSession, { type: 'system', subType: 1, member: getContextUser().name });
    },
  },
};
</script>

