<template>
  <div class="chat-window flex-column" style="flex-grow: 1;">
    <div class="flex-center chat-head">
      <div>
        <div class="flex-vertical-center">
          <p class="text-one-line">
            {{session.name}}
            <template v-if="session.type === 1 && Object.values(session.members).length !== 0">
              ({{Object.values(session.members).length}})
            </template>
          </p>
          <i v-if="session.type === 1"
            class="iconfont icon-bulk-invitation-o"
            @click="showChatSide = true"></i>
        </div>
      </div>
    </div>
    <history v-if="session" :id="session.id" :key="session.id" class="chat-history"></history>
    <chat-input :session="session"></chat-input>
    <transition name="slide">
      <ChatMember
        :session="session"
        @visible="showChatSide=false"
        v-if="showChatSide">
      </ChatMember>
    </transition>
  </div>
</template>

<script>
import History from '@/components/im/History';
import { changeGroupInfo } from '@/api/im';
import ChatInput from './ChatInput';
import ChatMember from './ChatMember';


export default {
  components: {
    History,
    ChatInput,
    ChatMember,
  },
  props: {
    session: Object,
  },
  data() {
    return {
      chatName: '',
      editStatus: false,
      showChatSide: false,
    };
  },
  methods: {
    editChatName() {
      this.editStatus = true;
      this.chatName = this.session.name;
      this.$nextTick(() => this.$refs.chatNameInput.focus());
      // this.$refs.chatNameInput.focus();
    },
    async changeChatName() {
      try {
        await changeGroupInfo({
          sessionId: this.session.id,
          name: this.chatName.trim(),
        });
        this.session.name = this.chatName.trim();
        this.editStatus = false;
      } catch (err) {
        if (err.code === -1) err.message = 'Operation failed, can not be empty string';
        this.$errHandler(err);
      }
    },
  },
};
</script>

