<template>
  <div>
    <el-form :model="form" :rules="rules" ref="form" @submit.native.prevent>
      <el-form-item prop="name">
        <el-input
          v-model="form.name"
          :placeholder="$t('file-list.im.rename_group')"
          @keyup.native.enter="submit" />
      </el-form-item>
    </el-form>

    <div slot="footer" class="text-align-right">
      <el-button round style="border:none" size="small" @click="$emit('cancel')">
        {{$t('public.dialog.cancel')}}
      </el-button>
      <el-button round type="primary" size="small" @click="submit">
        {{$t('public.dialog.confirm')}}</el-button>
    </div>
  </div>
</template>

<script>
import validator, { validateFormFields } from '@/utils/validators/rules-validation';

export default {
  name: 'DialogRenameGroup',
  props: {
    name: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      form: { name: '' },
      rules: {
        name: [
          validator.requirement(),
          validator.spaceTrim(),
          ...validator.charLengthRange(1, 40),
        ],
      },
    };
  },
  watch: {
    name: {
      handler(val) {
        this.form.name = val;
      },
      immediate: true,
    },
  },
  methods: {
    async submit() {
      try {
        await validateFormFields(this.$refs.form);
        this.$emit('confirm', this.form);
      } catch (err) {
        this.$errHandler(err, -2);
      }
    },
  },
};
</script>
