
import {
  defineComponent, ref,
} from '@vue/composition-api';
import i18n from 'i18next';
import Scrollable from '@/components/Scrollable.vue';


const Sidebar = defineComponent({

  props: {
    collections: {
      type: Array as () => string[],
      default: () => [],
    },
  },

  setup(props, { emit }) {
    const currentCollection = ref<string>(i18n.t('order-meeting.home.all'));

    function changeCollection(newV:string) {
      currentCollection.value = newV;
      emit('changeCollection', newV);
    }
    return {
      currentCollection,
      changeCollection,
    };
  },

  render() {
    const self = this as InstanceType<typeof Sidebar>;
    const { collections, currentCollection, changeCollection } = self;
    return <aside class="flex-column" style="position: relative;">
      <div class="flex-column sidebar-container">
        <Scrollable y class="flex-1">
          <ul class="list-container">
            {
              collections.map((collection: string) => {
                return <li
                  class={['list-item', 'flex-vertical-center', { active: currentCollection === collection }]}
                  onClick={changeCollection.bind(self, collection)}>
                  <span>{collection}</span>
                </li>;
              })
            }
          </ul>
        </Scrollable>
        <div class="analytics-tab flex-vertical-center" hidden={ !self.$can('system', 'OP0146') }
          onclick={ () => {
            self.$router.push({
              path: '/order-meeting/statistics',
              query: { workspaceId: self.$route.query.workspaceId },
            });
          }}>
          <i class="iconfont iconshujufenxi"></i>
          <span> { i18n.t('order-meeting.header.analytics') }</span>
        </div>
      </div>
    </aside>;
  },
});

export default Sidebar;
