<template>
  <column flex="1" class="app-container">
    <TheHeader
      class="the-header"
      :collections="collections"
      @changeCollection="collection=> {currentCollection = collection}"
      ref="header">
    </TheHeader>
    <div class="flex-column flex-1" style="position: relative; z-index: 1">
      <transition name="slide-fade" mode="out-in" appear>
        <div class="flex flex-1" v-if="$route.path.startsWith('/order-meeting/index')">
          <TheSidebar
            :collections="collections"
            @changeCollection="collection=> {currentCollection = collection}" />
          <router-view
            ref="home"
            :workspace-id="workspaceId"
            :currentCollection="currentCollection"
            :shoeList="shoeListOrderByCollection">
          </router-view>
        </div>
        <router-view
          :collections="collectionsWithoutAll"
          :allShoeList="shoeList"
          @add-to-wish="$refs.header.doAddWishAnimate()"
          :workspace-id="workspaceId"
          v-else>
        </router-view>
      </transition>

    </div>
  </column>
</template>

<script>
import Vue from 'vue';

import { getTradeFairList } from '@/api/order-meeting';
import i18n from 'i18next';
import TheHeader from './components/header';
import TheSidebar from './components/Sidebar';

/** @typedef {import('@/api/order-meeting').CustomizedAppInfo} CustomizedAppInfo */
/** @typedef {import('@/api/order-meeting').CatalogForTradeFair} CatalogForTradeFair */
/** @typedef {import('@/api/order-meeting').Collocation} Collocation */
/** @typedef {import('@/api/shoe').Version} Version */
/** @typedef {{items: string[], value: [number, number], maxScale: number}} GestureFrameOptions */

export default Vue.extend({
  name: 'OrderMeeting',
  props: {
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
  },
  components: {
    TheHeader,
    TheSidebar,
  },
  data() {
    return {
      currentCollection: i18n.t('order-meeting.home.all'),
      shoeList: [],
    };
  },
  computed: {
    collectionsWithoutAll() {
      return this.shoeList.reduce((list, shoe) => {
        if (!list.find(item => item === shoe.fileInfo.collection)) {
          list.push(shoe.fileInfo.collection);
        }
        return list;
      }, []);
    },
    collections() {
      return [i18n.t('order-meeting.home.all'), ...this.collectionsWithoutAll];
    },
    shoeListOrderByCollection() {
      if (this.currentCollection === i18n.t('order-meeting.home.all')) {
        return this.shoeList;
      }
      return this.shoeList.filter(it => it.fileInfo.collection === this.currentCollection);
    },
  },
  async created() {
    if (this.workspaceId) {
      localStorage.setItem('workspaceId', this.workspaceId);
    }

    this.shoeList = (await getTradeFairList(this.workspaceId)).data;
  },
});
</script>

<style lang="scss" scoped>
:fullscreen .the-header {
  display: none;
}
.app-container {
  min-width: 1200px;
  background-color: #fff;
}

.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-enter,
.slide-fade-leave-to {
  // transform: translateY(-20px);
  opacity: 0;
}
</style>
