<template>
  <div class="chat-input">
    <div class="el-textarea">
      <textarea
        ref="input"
        autocomplete="off"
        v-model="message"
        rows="8"
        :placeholder="$t('file-list.im.send_message')"
        class="el-textarea__inner"
        @keydown.enter.prevent="$act(sendMsg, ['text', message])"
        @paste="$act(handlePaste, [$event])"
      ></textarea>
    </div>
    <div class="flex-space-between" style="height: 70px">
      <div class="tools">
        <upload-file-item
          :max-size="10 * 1024 * 1024"
          ref="uploadEditImgInput"
          :draggable="false"
          accept=".jpg, .jpeg, .png, .gif"
          @upload="sendMsg('image', $event)"
        >
          <i class="iconfont icon-picture"></i>
        </upload-file-item>
        <el-popover v-if="isEmojiSupport" trigger="hover" v-model="emojiVisible">
          <EmojiSelector style="height: 250px; width: 250px" @input="insertEmoji" />
          <i class="iconfont icon-emoji" slot="reference"></i>
        </el-popover>
      </div>
      <el-button
        round
        :loading="$isActing(sendMsg)"
        size="small"
        style="width: 88px; margin-right: 20px"
        :disabled="!message"
        type="primary"
        @click="$act(sendMsg, ['text', message])"
        >{{ $t("file-list.im.send") }}</el-button
      >
    </div>
  </div>
</template>

<script>
import UploadFileItem from '@/components/upload/index.vue';
import EmojiSelector from '@/components/emoji/Selector.vue';
import isEmojiSupport from '_/is-emoji-support';

export default {
  components: {
    EmojiSelector,
    UploadFileItem,
  },
  props: {
    session: Object,
  },
  data() {
    return {
      emojiVisible: false,
      message: '',
      isEmojiSupport: isEmojiSupport(),
    };
  },
  methods: {
    handlePaste(event) {
      const { items } = event.clipboardData || event.originalEvent.clipboardData;
      return Promise.all(
        Array.from(items)
          .filter(item => item.kind === 'file')
          .map(async (item) => {
            await this.$im.send(this.session, item.getAsFile());
          }),
      );
    },
    insertEmoji(char) {
      this.message = `${this.message}${char}`;
      this.emojiVisible = false;
      this.$refs.input.focus();
    },
    /**
     * @param {String} type 键入发送消息的类型，text为文字，image为图片
     */
    async sendMsg(type, msg) {
      let msgBody;
      switch (type) {
        case 'text':
          this.message = '';
          msg = msg.trim();
          if (!msg) return;
          msgBody = { type, content: msg };
          break;
        case 'image': msgBody = { type, blob: msg };
          break;
        default:
      }
      await this.$im.send(this.session, msgBody);
    },
  },
};
</script>

