  display: flex;
}

.chat-input {
  height: 180px;
  border-top: 2px solid #ddd;
}

.el-textarea /deep/.el-textarea__inner {
  padding: 16px 16px 0;
  height: 109px;
  border: none;
  resize: none;
  outline: none;
}

.tools .iconfont {
  padding: {
    left: 0.25em;
    right: 0.25em;
  }
  color: #c5c7cb;
  font-size: 24px;
  cursor: pointer;
  &:hover {
    color: #2878ff;
  }
}

.is-disabled {
  color: #fff;
  background-color: rgba(40, 120, 255, 1);
  opacity: 0.2;
  &:hover {
    background: rgba(40, 120, 255, 1);
    color: #fff;
  }
}
