
<template>
  <header class="header flex-space-between">
    <img src="@/assets/img/header/order-meeting-header-logo.png" width="141"
      style="cursor: pointer"
      @click="$router.push({
        path: '/order-meeting/index',
        query: { workspaceId: $route.query.workspaceId },
      })">
    <div class="flex-center header-ctrl">
      <!-- v-if="$can('system', 'OP0147')" -->
      <i class="iconfont icongouwuche"
        :class="{'icongouwuche-en': $i18n.i18next.language === 'en'}"
        @click="$router.push({
          path: '/order-meeting/intention/form',
          query: { workspaceId: $route.query.workspaceId },
        })">
      </i>
      <i class="iconfont iconIM"
        :class="{'iconIM-en': $i18n.i18next.language === 'en'}"
        @click="openIm">
        <i :class="{ unread: unread > 0}"></i>
      </i>
      <el-dropdown trigger="hover" class="dropdown-wrapper">
        <i class="iconfont iconzhongyingqiehuan" style="outline:none;color:#03234C;"></i>
        <el-dropdown-menu slot="dropdown" style="padding: 4px 0;">
          <el-dropdown-item @click.native="changeLang('2')"
            class="flex-space-between dropdown-item" style="width: 100px;">
            简体中文
          </el-dropdown-item>
          <el-dropdown-item @click.native="changeLang('1')"
            class="flex-space-between dropdown-item" style="width: 100px;">
            English
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-dropdown trigger="hover" divided class="dropdown-wrapper">
        <span class="flex-center" style="cursor:pointer">
          <BaseImage class="avatar" :src="userImage">
          </BaseImage>
          <i class="el-icon-arrow-down"></i>
        </span>
        <el-dropdown-menu v-if="!isQt" slot="dropdown" style="padding: 4px 0;">
          <el-dropdown-item class="dropdown-item dropdown-item-name">
            {{ user.account || user.email || user.mail || user.name  }}
          </el-dropdown-item>
          <el-dropdown-item @click.native="logout" class="dropdown-item">
            <i class="iconfont icon-logout"
              style="color:#03234C;font-size:16px;padding-right: 10px;"></i>
            {{ $t('order-meeting.header.logout') }}
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>

    <ChatPanel
      :shareChatId="shareChatId"
      :showChatList="!isVirtualAccount"
      :workspaceId="workspaceId"
      isOrderMeeting
      v-if="chating"
      @update:visible="chating = false"
      width="636px">
    </ChatPanel>
  </header>
</template>

<script>
import Vue from 'vue';

import setLang, {
  languageNumber2lang,
} from '@/router/utils/set-lang';
import { createChat } from '@/api/im';
// import BaseUser from '@/components/base/User';
import BaseImage from '@/components/base/Image';
import ChatPanel from '../../file-list/components/im/ChatPanel';
import { logout, getContextUser } from '../../../loaders/context';
import { getUnread, getSessions } from '../../../loaders/im';

export default Vue.extend({
  name: 'OrderMeetingHeader',
  components: {
    ChatPanel,
    // BaseUser,
    BaseImage,
  },
  props: {
    collections: Array,
  },
  data() {
    return {
      chating: false,
      workspaceId: '',
    };
  },
  created() {
    this.workspaceId = localStorage.getItem('workspaceId');
  },
  methods: {
    // doAddWishAnimate() {
    //   const item = this.tagList.find(_item => _item.label === 'intention');
    //   item.badge = '+1';
    //   if (item.timeout) {
    //     clearTimeout(item.timeout);
    //   }
    //   item.timeout = setTimeout(() => {
    //     item.badge = '';
    //   }, 2000);
    // },
    async openIm() {
      // console.info(this.workspaceId);
      if (this.isVirtualAccount
        && getSessions(this.$route.query.workspaceId || this.workspaceId).length === 0) {
        const sessId = (await createChat(this.workspaceId)).data;
        const newSess = await this.$im.getSession({ id: sessId });
        await this.$im.prependSess(newSess);
      }
      console.info(this.shareChatId);
      this.chating = true;
    },
    logout() {
      logout();
      localStorage.removeItem('workspaceId');
      // Jump to login page
      this.$router.push({
        name: 'Login',
        query: {
          redirect: '/order-meeting',
        },
      });
    },
    changeLang(langNum) {
      const lang = languageNumber2lang(langNum);

      setLang(lang);
    },
  },
  computed: {
    sessions() {
      return getSessions();
    },
    shareChatId() {
      return this.sessions?.[0]?.id || '';
    },
    unread() {
      return getUnread(this.workspaceId);
    },
    isQt() {
      return navigator.userAgent.includes('Qt');
    },
    isVirtualAccount() {
      return this.user.roleId === '3';
    },
    user() {
      return getContextUser();
    },
    userImage() {
      return this.user.headImg || this.user.avatar || this.user.icon || require('@/assets/img/header/default_avatar.png');
    },
  },
});
</script>

