<template>
  <aside class="member-container">
    <div class="mask" tabindex="0" @focus="$emit('visible')"></div>
    <div class="flex-column" style="position: relative;height:100%">
      <div class="flex-space-around group-name">
        <OmittedDisplayer class="flex-1">{{session.name}}</OmittedDisplayer>
        <i class="iconfont icon-write" @click="rename"></i>
      </div>
      <div class="member-list-top flex-space-around">
        <span class="flex-1"> {{$t('file-list.im.members_count')}} ({{memberList.length||0}})</span>
        <i class="iconfont el-icon-circle-plus-outline"
          @click="addMember"
          v-if="$can('workspace', 'OP2003')"></i>
      </div>
      <Scrollable y class="flex-1">
        <ul>
          <li class="flex-space-around member"
            v-for="member of memberList" :key="member.id">
            <BaseUser :user="member" class="flex-1">
              {{member.name ||user.account|| user.email || user.mail }}
            </BaseUser>
            <i class="iconfont icondelmuber"
              v-if="isAdmin && member.groupRole !== 0 "
              @click="removeMember(member)"></i>
          </li>
        </ul>
      </Scrollable>
      <div class="quit flex-space-around" @click="quit" style="cursor: pointer;">
        <span class="flex-1">
          {{ $t('file-list.im.leave_group')}}
        </span>
        <i class="iconfont icon-angle-right"></i>
      </div>
    </div>
  </aside>
</template>

<script>
import i18n from 'i18next';
import OmittedDisplayer from '@/components/OmittedDisplayer';
import Scrollable from '@/components/Scrollable';
import BaseUser from '@/components/base/User';
import {
  removeGroupMember, quitGroup, addGroupMember, changeGroupInfo,
} from '@/api/im';
import { getSessions } from '../../../../loaders/im';
import { getContextUser } from '../../../../loaders/context';
import AddMember from './AddMember';
import DialogRenameGroup from './dialogComponents/DialogRenameGroup';


export default {
  name: 'ChatMemeber',
  components: {
    Scrollable,
    OmittedDisplayer,
    BaseUser,
    // eslint-disable-next-line vue/no-unused-components
    AddMember,
  },
  props: {
    groupAppId: {
      default() {
        return this.$route.query.relateId;
      },
    },

    session: {
      type: Object,
    },
  },
  data() {
    return {
      // memberList: [],
    };
  },
  computed: {
    isAdmin() {
      return this.memberList.filter(it => it.id === getContextUser().id)[0].groupRole === 0;
    },
    memberList() {
      return Object.values(this.session.members);
    },
  },
  // async mounted() {
  //   if (this.memberList.length === 0) {
  //     this.session.getMembers();
  //   }
  // },
  methods: {
    async removeMember(member) {
      const confirm = await this.$confirmBool(
        i18n.t('file-list.im.remove_confirm_text'),
        i18n.t('file-list.im.remove_dialog_title'),
        {
          confirmButtonText: i18n.t('file-list.im.remove'),
          cancelButtonText: i18n.t('public.dialog.cancel'),
        },
      );
      if (!confirm) return;
      await removeGroupMember(this.session.id, [member.id]);
      this.$im.send(this.session, {
        type: 'system',
        subType: 5,
        member: member.name || member.account || member.email || member.mail,
      });
    },
    async quit() {
      const msg = this.memberList.length === 1
        ? i18n.t('file-list.im.last_member_quit_text')
        : i18n.t('file-list.im.member_quit_text');
      const confirm = await this.$confirmBool(msg, i18n.t('file-list.im.quit_dialog_title'), {
        confirmButtonText: i18n.t('file-list.im.leave'),
        cancelButtonText: i18n.t('public.dialog.cancel'),
      });
      if (!confirm) return;
      const user = getContextUser();
      await this.$im.send(this.session, {
        type: 'system',
        subType: 4,
        member: user.name
          || user.email
          || user.account,
      });
      await quitGroup(this.session.id);
      // getSessions().splice(getSessions().indexOf(this.session.id), 1);
      this.$emit('visible');
      this.$closest('ChatPanel').currentSession = null;
    },
    async addMember() {
      const addMember = await this.$Dialog.dialogify({
        is: AddMember,
        props: {
          includeList: this.memberList.map(member => member.id),
        },
      }, {
        props: {
          title: i18n.t('file-list.im.member_add_dialog_title'),
          width: '596px',
        },
      });
      await addGroupMember(this.session.id, this.groupAppId, addMember.map(it => it.id));
      Promise.all(addMember.map(member => {
        return this.$im.send(this.session, {
          type: 'system',
          subType: 3,
          member: member.name || member.email || member.account,
        });
      }));
      // this.memberList = (await getGroupUsers(this.session.id)).data;
      // console.info(addMember);
    },
    // 重命名群聊
    async rename() {
      const formData = await this.$Dialog.dialogify({
        is: DialogRenameGroup,
        props: {
          name: this.session.name,
        },
      }, {
        props: {
          title: i18n.t('file-list.im.rename_chatName'),
          width: '400px',
          titleAlign: 'left',
          closeOnClickModal: false,
        },
      });
      if (formData.name === this.session.name) {
        return;
      }
      try {
        await changeGroupInfo({
          sessionId: this.session.id,
          name: formData.name,
        });
      } catch (err) {
        if (err.code === -1) err.message = i18n.t('file-list.im.rename_input_empty');
        this.$errHandler(err);
      }
      const user = getContextUser();
      await this.$im.send(this.session, {
        type: 'system',
        subType: 6,
        member: user.name
          || user.email
          || user.account,
        oldGroupName: this.session.name,
        newGroupName: formData.name,
      });
      this.$im.getSession({ ...this.session, name: formData.name }, true);
    },
  },

};
</script>

